p {
    @include fontSize(16px);

    letter-spacing: .2px;
    margin: 10px 0 0;
    padding: 0;

    &.lead {
        @include fontSize(20px);
        margin-left: $spacingLg;

        letter-spacing: .5px;
        font-family: $fontHighlight;

        color: $colorGreyDarker;
        text-shadow: 1px 1px 1px rgba(127, 127, 127, .3);
    }
    &.subtle {
        @include fontSize(12px);
        margin: 0;
        margin-left: $spacingBase;
    }
}
