.headerMain {
    @extend %flex;

    position: relative;

    flex: 0 0 auto;

    width: 100%;
    padding: $spacingSm $spacingBase;

    font-family: $fontHighlight;

    color: $colorGreyDark;
    background: $colorGreyLighter;

    .headerMain-logo {
        @extend %flex;

        align-items: center;

        max-width: 50%;

        img {
            width: 100px;
            margin-right: 15px;
        }
    }
}
