@mixin mq($mq) {
    @if $mq == xsm-only {
        @media only screen and (min-width:$startXSm) and (max-width:$endXSm) {
            @content;
        }
    }
    @else if $mq == xsm {
        @media only screen and (min-width:$startXSm) {
            @content;
        }
    }
    @else if $mq == sm-only {
        @media only screen and (min-width:$startSm) and (max-width:$endSm) {
            @content;
        }
    }
    @else if $mq == sm {
        @media only screen and (min-width:$startSm) {
            @content;
        }
    }
    @else if $mq == med-only {
        @media only screen and (min-width:$startMed) and (max-width:$endMed) {
            @content;
        }
    }
    @else if $mq == med {
        @media only screen and (min-width:$startMed) {
            @content;
        }
    }
    @else if $mq == lg-only {
        @media only screen and (min-width:$startLg) and (max-width:$endLg) {
            @content;
        }
    }
    @else if $mq == lg {
        @media only screen and (min-width:$startLg) {
            @content;
        }
    }
    @else if $mq == xlg-only {
        @media only screen and (min-width:$startXLg) and (max-width:$endXLg) {
            @content;
        }
    }
    @else if $mq == xlg {
        @media only screen and (min-width: $startXLg) {
            @content;
        }
    }
    @else if $mq == xxlg {
        @media only screen and (min-width: $startXXLg) {
            @content;
        }
    }
    @else {
        @media only screen and (min-width: $mq) {
            @content;
        }
    }
}
