.row {
    @extend %contained;

    display: block;

    padding: 0 $spacingBase;

    text-align: left;
    letter-spacing: -.31em;
    font-size: 0;

    &--fullWidth {
        max-width: none;
        padding: 0 0 $spacingXLg;
    }
}

.column {
    @include fontSize($fontSizeBase);

    display: inline-block;

    width: 100%;
    margin: 0;
    padding: 0;

    text-align: left;
    vertical-align: top;
    letter-spacing: normal;
}

